body {
    background-color: #383aaf;
  }
  .Card {
    margin: 20px;
  }
  
  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  
  .items {
    background-color: #f5f5f5;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .items:hover {
    transform: translateY(-5px);
  }
  
  .price {
    text-align: center;
    margin-bottom: 20px;
  }
  
  h1 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #333;
  }
  
  h3 {
    font-size: 16px;
    margin-bottom: 5px;
    color: #666;
  }
  
  .primary-btn {
    display: inline-block;
    text-align: center;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    padding: 10px 20px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .primary-btn:hover {
    background-color: #0056b3;
  }
  
  .search-container {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    margin-left: 12vw;
  }
  
  .search-container input[type="text"] {
    padding: 10px;
    width: 73vw;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .search-container input[type="text"]::placeholder {
    color: #999;
  }
  
  .search-container button:hover {
    background-color: #0056b3;
  }
  
  @media screen and (max-width: 768px) {
    .container {
      grid-template-columns: 1fr;
    }
    .search-container {
      margin-left: 5vw;
    }
    .search-container input[type="text"] {
      width: 80vw;
    }
  }